___CSS_LOADER_EXPORT___.push([module.id, "\nbody,\nhtml {\n    height: 100%\n}\nbody {\n    margin: 0;\n    padding: 0\n}\n#app {\n    font-family: Avenir, Helvetica, Arial, sans-serif;\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-font-smoothing: grayscale;\n    color: #2c3e50;\n    overflow: hidden;\n    padding: 120px 20px 50px;\n    height: 100%;\n    background: #fff\n}\n#app,\n#app header {\n    text-align: center;\n    width: 100%;\n    box-sizing: border-box\n}\n#app header {\n    position: absolute;\n    top: 0;\n    left: 0;\n    height: 120px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center\n}\n#app header h1,\n#app header h2 {\n    font-weight: 400;\n    margin: 0\n}\n#app header h1 {\n    font-size: 24px\n}\n#app header h2 {\n    font-size: 14px\n}\n#app header h2 a {\n    color: inherit\n}\n#app header a {\n    font-size: 12px;\n    text-decoration: underline;\n    margin-top: 10px;\n    cursor: pointer\n}\n#app .info {\n    font-size: 12px;\n    color: #999\n}\n#app .info a {\n    font-style: italic;\n    color: inherit\n}\n#app .cssloading-circle {\n    background: #eee\n}\n#app .vue-recyclist-item {\n    contain: layout\n}\n", "",{"version":3,"sources":["example/App.vue"],"names":[],"mappings":";AAgIA;;IAEA;AACA;AAEA;IACA,SAAA;IACA;AACA;AAEA;IACA,iDAAA;IACA,mCAAA;IACA,kCAAA;IACA,cAAA;IACA,gBAAA;IACA,wBAAA;IACA,YAAA;IACA;AACA;AAEA;;IAEA,kBAAA;IACA,WAAA;IACA;AACA;AAEA;IACA,kBAAA;IACA,MAAA;IACA,OAAA;IACA,aAAA;IACA,aAAA;IACA,sBAAA;IACA,uBAAA;IACA;AACA;AAEA;;IAEA,gBAAA;IACA;AACA;AAEA;IACA;AACA;AAEA;IACA;AACA;AAEA;IACA;AACA;AAEA;IACA,eAAA;IACA,0BAAA;IACA,gBAAA;IACA;AACA;AAEA;IACA,eAAA;IACA;AACA;AAEA;IACA,kBAAA;IACA;AACA;AAEA;IACA;AACA;AAEA;IACA;AACA","file":"App.vue","sourcesContent":["<template>\n  <div id=\"app\">\n    <header>\n      <h1>VueRecyclist</h1>\n      <h2>Infinite scroll list for Vue.js with DOM recycling. <a href=\"https://github.com/xtongs/vue-recyclist\">Github</a></h2>\n      <a @click=\"tombstone = !tombstone\">{{ tombstone ? 'hide' : 'show'}} tombstones</a>\n    </header>\n    <vue-recyclist class=\"list\" :list=\"list\" :tombstone=\"tombstone\" :size=\"size\" :loadmore=\"loadmore\">\n      <template v-slot:tombstone>\n        <div class=\"item tombstone\">\n          <div class=\"avatar\"></div>\n          <div class=\"bubble\">\n            <p></p>\n            <p></p>\n            <p></p>\n            <div class=\"meta\">\n              <time class=\"posted-date\"></time>\n            </div>\n          </div>\n        </div>\n      </template>\n      <template v-slot:item=\"props\">\n        <div :id=\"props.data.id\" class=\"item\" @click=\"itemClicked(props)\">\n          <div class=\"avatar\" :style=\"{backgroundImage: 'url(' + (props.data.avatar || '') + ')'}\"></div>\n          <div class=\"bubble\">\n            <p>{{ props.data.msg }}</p>\n            <div class=\"meta\">\n              <time class=\"posted-date\">{{ props.data.time }}</time>\n            </div>\n          </div>\n        </div>\n      </template>\n      <!--<div slot=\"spinner\">Loading Data</div>-->\n      <!--<div slot=\"nomore\">No More Data</div>-->\n    </vue-recyclist>\n    <div ref=\"avatars\" style=\"display:none\">\n      <img src=\"./images/avatar0.jpg\"/>\n      <img src=\"./images/avatar1.jpg\"/>\n      <img src=\"./images/avatar2.jpg\"/>\n      <img src=\"./images/avatar3.jpg\"/>\n    </div>\n    <p class=\"info\">Inspired by <a href=\"https://developers.google.com/web/updates/2016/07/infinite-scroller\">Complexities of an Infinite Scroller</a></p>\n  </div>\n</template>\n\n<script>\n  import Data from './data'\n  import Stats from './stats.min'\n  import VueRecyclist from '../src/index'\n  export default {\n    name: 'app',\n    data() {\n      return {\n        // data\n        initTime: new Date().getTime(),\n        id: 0,\n        // list\n        list: [],\n        size: 20,\n        tombstone: !+localStorage['tombstone']\n      }\n    },\n    components: {\n      'vue-recyclist': VueRecyclist\n    },\n    watch: {\n      tombstone(val) {\n        localStorage['tombstone'] = +!val\n        this.id = 0\n        this.list = []\n        this.loadmore()\n      }\n    },\n    created() {\n      this.addStatsPanel()\n    },\n    methods: {\n      getItem(id) {\n        const avatar = Math.floor(Math.random() * Data.avatars)\n        const msg = Data.messages[Math.floor(Math.random() * Data.messages.length)]\n        return {\n          id: 10000 + id,\n          avatar: this.$refs.avatars.children[avatar].src,\n          msg: msg,\n          time: new Date(Math.floor(this.initTime + id * this.size * 1000 + Math.random() * this.size * 1000)).toString(),\n        }\n      },\n      loadmore() {\n        let items = []\n        setTimeout(() => {\n          for (let i = 0; i < this.size; i++) {\n            items.push(this.getItem(this.id++))\n          }\n          this.list = this.list.concat(items)\n        }, 2000)\n      },\n      itemClicked(props) {\n        console.log('Item:' + props.index, props.data)\n      },\n      addStatsPanel() {\n        if (window.requestIdleCallback) {\n          let self = this\n          let stats = new Stats()\n          let domPanel = new Stats.Panel('D', '#0ff', '#002')\n          stats.addPanel(domPanel)\n          stats.showPanel(3)\n          document.body.appendChild(stats.dom)\n          setTimeout(function timeoutFunc() {\n            // Only update DOM node graph when we have time to spare to call\n            // numDomNodes(), which is a fairly expensive function.\n            requestIdleCallback(() => {\n              domPanel.update(self.numDomNodes(document.body), 1500)\n              setTimeout(timeoutFunc, 100)\n            })\n          }, 100)\n        }\n      },\n      numDomNodes(node) {\n        if(!node.children || node.children.length == 0) return 0\n        let childrenCount = Array.from(node.children).map(this.numDomNodes)\n        return node.children.length + childrenCount.reduce(function(p, c){ return p + c; }, 0)\n      }\n    }\n  }\n\n</script>\n\n<style>\n    body,\n    html {\n        height: 100%\n    }\n\n    body {\n        margin: 0;\n        padding: 0\n    }\n\n    #app {\n        font-family: Avenir, Helvetica, Arial, sans-serif;\n        -webkit-font-smoothing: antialiased;\n        -moz-osx-font-smoothing: grayscale;\n        color: #2c3e50;\n        overflow: hidden;\n        padding: 120px 20px 50px;\n        height: 100%;\n        background: #fff\n    }\n\n    #app,\n    #app header {\n        text-align: center;\n        width: 100%;\n        box-sizing: border-box\n    }\n\n    #app header {\n        position: absolute;\n        top: 0;\n        left: 0;\n        height: 120px;\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        align-items: center\n    }\n\n    #app header h1,\n    #app header h2 {\n        font-weight: 400;\n        margin: 0\n    }\n\n    #app header h1 {\n        font-size: 24px\n    }\n\n    #app header h2 {\n        font-size: 14px\n    }\n\n    #app header h2 a {\n        color: inherit\n    }\n\n    #app header a {\n        font-size: 12px;\n        text-decoration: underline;\n        margin-top: 10px;\n        cursor: pointer\n    }\n\n    #app .info {\n        font-size: 12px;\n        color: #999\n    }\n\n    #app .info a {\n        font-style: italic;\n        color: inherit\n    }\n\n    #app .cssloading-circle {\n        background: #eee\n    }\n\n    #app .vue-recyclist-item {\n        contain: layout\n    }\n</style>\n\n<style>\n    .list {\n        width: 375px;\n        max-width: 100%;\n        height: 100%;\n        margin: 0 auto;\n        padding: 0;\n        border: 1px solid #ddd;\n        list-style-type: none;\n        text-align: center;\n        background: #eee\n    }\n\n    .list .item {\n        display: flex;\n        padding: 10px 0;\n        width: 100%;\n        text-align: left\n    }\n\n    .list .item .avatar {\n        border-radius: 50%;\n        margin-left: 15px;\n        margin-right: 6px;\n        min-width: 48px;\n        width: 48px;\n        height: 48px;\n        background-image: url('./images/unknown.jpg');\n        background-size: cover;\n        outline: none\n    }\n\n    .list .item p {\n        margin: 0;\n        word-wrap: break-word;\n        font-size: 14px\n    }\n\n    .list .item.tombstone p {\n        width: 100%;\n        height: .5em;\n        background-color: #ccc;\n        margin: .5em 0\n    }\n\n    .list .item .bubble {\n        padding: 7px 10px;\n        color: #333;\n        background: #fff;\n        box-shadow: 0 3px 2px rgba(0, 0, 0, .1);\n        position: relative;\n        max-width: 420px;\n        min-width: 80px;\n        margin: 0 20px 0 5px\n    }\n\n    .list .item .bubble:before {\n        content: '';\n        border-style: solid;\n        border-width: 0 10px 10px 0;\n        border-color: transparent #fff transparent transparent;\n        position: absolute;\n        top: 0;\n        left: -10px;\n    }\n    .list .item .meta {\n        font-size:.8rem;\n        color:#999;\n        margin-top:3px;\n    }\n</style>\n"]}]);
